.button {
  display: flex;
  align-items: center;
  padding: 0px 36px 0px 16px;
  cursor: pointer;
  min-height: 36px;
}
.button:hover {
  background-color: var(--yt-spec-10-percent-layer);
}
/* youtube studio dropdown popups use different color for hover */
ytls-menu-popup-renderer .button:hover {
  background-color: var(--yt-spec-white-1-alpha-20);
}
.buttonLabel {
  color: var(--yt-spec-text-primary);
  white-space: nowrap;
  font-family: sans-serif;
  font-size: 1.4rem;
  line-height: 2rem;
  font-weight: 100;
  -webkit-font-smoothing: var(--paper-font-subhead_-_-webkit-font-smoothing);
}
.buttonIconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  width: 24px;
  height: 24px;
}
.buttonIconContainer .buttonIcon {
  width: 20px;
  height: 20px;
  flex: none;
  filter: grayscale(100%) brightness(0%);
  background: url(assets/logos/logo.svg) no-repeat 0 0px;
  background-size: cover;
}
html[dark] .buttonIcon {
  filter: grayscale(100%) brightness(500%);
}
.icon {
  width: 24px;
  height: 24px;
  margin-right: 16px;
}
:global {
  /* fixes youtube's chat menu dropdown font size */
}
:global .bttv-youtube #bttvSettingsPanel,
:global .bttv-youtube .bttv-rs-modal-wrapper {
  font-family: 'Helvetica Neue', Helvetica, Arial;
  color: var(--bttv-rs-text-primary);
  font-size: 1.3rem;
  line-height: 1.5;
}
:global .bttv-youtube .bttv-rs-toggle-checked .bttv-rs-toggle-presentation {
  background-color: var(--paper-toggle-button-checked-button-color);
}
:global .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-sidenav-item.bttv-rs-sidenav-item-active,
:global .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-dropdown-item.bttv-rs-dropdown-item-active,
:global .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-dropdown.bttv-rs-dropdown-selected-within .bttv-rs-dropdown-toggle-icon {
  color: var(--yt-spec-call-to-action);
}
:global .bttv-youtube .bttv-rs-btn-primary,
:global .bttv-youtube .bttv-rs-btn-icon-with-text.bttv-rs-btn-primary > .bttv-rs-icon {
  background-color: var(--yt-spec-call-to-action);
  color: #ffffff;
}
:global .bttv-youtube .bttv-rs-btn-primary:active,
:global .bttv-youtube .bttv-rs-btn-primary:hover,
:global .bttv-youtube .bttv-rs-btn-primary:focus,
:global .bttv-youtube .bttv-rs-btn-icon-with-text.bttv-rs-btn-primary:hover > .bttv-rs-icon,
:global .bttv-youtube .bttv-rs-btn-icon-with-text.bttv-rs-btn-primary:focus > .bttv-rs-icon {
  background-color: var(--yt-spec-call-to-action);
  color: #ffffff;
}
:global .bttv-youtube .bttv-rs-checkbox-checked .bttv-rs-checkbox-wrapper .bttv-rs-checkbox-inner::before,
:global .bttv-youtube .bttv-rs-checkbox-indeterminate .bttv-rs-checkbox-wrapper .bttv-rs-checkbox-inner::before {
  background-color: var(--paper-checkbox-checked-color);
  border-color: var(--paper-checkbox-checked-color);
}
:global .bttv-youtube .bttv-rs-checkbox label:hover .bttv-rs-checkbox-inner::before {
  border-color: var(--paper-checkbox-checked-color);
}
:global .bttv-youtube .bttv-rs-auto-complete-item:hover,
:global .bttv-youtube .bttv-rs-auto-complete-item:focus,
:global .bttv-youtube .bttv-rs-auto-complete-item-focus {
  background-color: rgba(0, 0, 0, 0.1);
  color: var(--bttv-rs-text-primary);
}
:global .bttv-youtube .bttv-rs-modal a {
  color: var(--bttv-rs-text-primary);
}
:global .bttv-youtube .bttv-rs-panel a {
  color: var(--yt-spec-call-to-action);
}
:global html[dark] .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-sidenav-item.bttv-rs-sidenav-item-active,
:global html[dark] .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-dropdown-item.bttv-rs-dropdown-item-active,
:global html[dark] .bttv-youtube .bttv-rs-sidenav-default .bttv-rs-dropdown.bttv-rs-dropdown-selected-within .bttv-rs-dropdown-toggle-icon {
  color: #ffffff;
}
:global html[dark] .bttv-youtube .bttv-rs-auto-complete-item:hover,
:global html[dark] .bttv-youtube .bttv-rs-auto-complete-item:focus,
:global html[dark] .bttv-youtube .bttv-rs-auto-complete-item-focus {
  background-color: var(--bttv-rs-dropdown-item-bg-hover);
}
:global html[dark] .bttv-youtube .bttv-rs-modal a {
  color: rgba(255, 255, 255, 0.5);
}
:global html[dark] .bttv-youtube .bttv-rs-panel a {
  color: var(--yt-spec-call-to-action);
}
:global tp-yt-paper-item.ytls-menu-service-item-renderer {
  font-size: 1.4rem;
}
