.bttv-hide-player-extensions .extensions-dock__layout,
.bttv-hide-player-extensions .extensions-video-overlay-size-container,
.bttv-hide-player-extensions .extensions-notifications,
.bttv-hide-player-extensions .extension-container,
.bttv-hide-player-extensions .extension-taskbar {
  display: none !important;
}
.bttv-hide-player-cursor div[data-test-selector='video-player__video-container'] {
  cursor: none;
}
#bttv-picture-in-picture {
  display: inline-flex !important;
}
#bttv-picture-in-picture button {
  position: relative;
  vertical-align: middle;
  overflow: hidden;
  text-decoration: none;
  white-space: nowrap;
  font-weight: var(--font-weight-semibold);
  border-radius: var(--border-radius-medium);
  font-size: var(--button-text-default);
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  user-select: none;
  height: var(--button-size-default);
  width: var(--button-size-default);
  border: var(--border-width-default) solid transparent;
  background-color: var(--color-background-button-icon-overlay-default);
  color: var(--color-text-button-overlay);
  padding: 4px;
}
#bttv-picture-in-picture button:hover {
  color: var(--color-text-button-overlay-hover);
  background-color: var(--color-background-button-icon-overlay-hover);
}
#bttv-picture-in-picture button div {
  width: 2rem;
  height: 2rem;
}
#bttv-picture-in-picture svg {
  fill: currentColor;
}
