#bttv-custom-timeout-contain {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 83px;
  height: 224px;
  overflow: hidden;
  background: rgba(90, 90, 90, 0.4);
  z-index: 99999;
}
#bttv-custom-timeout-contain .text,
#bttv-custom-timeout-contain .cursor {
  position: absolute;
  left: 0px;
  top: 100px;
  width: 80px;
  height: 1px;
  background: #f00;
}
#bttv-custom-timeout-contain .text {
  top: 85px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background: rgba(0, 0, 0, 0.6);
  cursor: default;
  display: none;
  color: #d3d3d3;
}
#bttv-custom-timeout-contain:hover .text {
  display: block;
}
#bttv-custom-timeout-contain:hover .cursor {
  background: #0f0;
}
