#bttvSettingsPanel #logo {
  padding: 10px;
  vertical-align: bottom;
  text-shadow: 1px 1px 3px #000000;
  font-size: 25px;
  font-family: 'Korolev-Bold', 'HelveticaNeue-CondensedBold', 'ArialNarrow-Bold', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
.bttvSettingsDropDownWrapper {
  position: relative !important;
  width: 100% !important;
}
.bttvSettingsDropDown {
  border-radius: var(--border-radius-medium);
  display: block;
  width: 100%;
  color: inherit;
}
.bttvSettingsDropDown .dropdownContainer {
  display: flex !important;
  -webkit-box-align: center !important;
  align-items: center !important;
  position: relative !important;
  padding: 0.5rem !important;
}
.bttvSettingsDropDown .dropdownContainer .dropdownIcon {
  display: flex !important;
  -webkit-box-align: center !important;
  align-items: center !important;
  padding-right: 0.5rem !important;
  flex-shrink: 0 !important;
}
.bttvSettingsDropDown .dropdownContainer .dropdownIcon .dropdownIconContainer {
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  margin: 2px;
  width: 16px;
  height: 16px;
}
.bttvSettingsDropDown .dropdownContainer .dropdownIcon .dropdownIconAspect {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.bttvSettingsDropDown .dropdownContainer .dropdownIcon .dropdownIconAspect .dropdownIconSpacer {
  padding-bottom: 100%;
}
.bttvSettingsDropDown .dropdownContainer .dropdownIcon .dropdownIconAspect > :not(.dropdownIconSpacer) {
  position: absolute;
  left: 0px;
  width: 100%;
  min-height: 100%;
  top: 0px;
}
.bttvSettingsDropDown .dropdownContainer .dropdownLabel {
  -webkit-box-flex: 1 !important;
  flex-grow: 1 !important;
}
.bttvSettingsDropDown:hover {
  color: inherit;
  text-decoration: none;
  background-color: var(--color-background-interactable-hover);
}
.bttvSettingsDropDown .bttvSettingsIconDropDown {
  filter: grayscale(100%) brightness(0%);
  background: url(assets/logos/logo.svg) no-repeat 0 0px;
  background-size: 16px 16px;
  opacity: 0.9;
}
.tw-root--theme-dark .bttvSettingsIconDropDown {
  filter: grayscale(100%) brightness(500%);
}
