.heading {
  color: var(--bttv-rs-text-heading);
}
.upper {
  text-transform: uppercase;
}
.description {
  color: var(--bttv-rs-text-secondary);
}
.header {
  position: absolute;
  right: 14px;
  left: 56px;
  top: 0;
  z-index: 10;
  overflow: hidden;
  pointer-events: none;
}
.header .flexHeader {
  pointer-events: all;
  display: flex;
  column-gap: 10px;
  padding: 10px 0 10px 10px;
  background-color: var(--bttv-rs-bg-overlay);
}
.header .closeButton {
  pointer-events: all;
  padding: 10px 0 10px 10px;
  float: right;
}
.setting {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
}
.settingDescription {
  color: var(--bttv-rs-text-secondary);
  margin-right: 8px;
}
.content {
  composes: scroll from '../../emote_menu/styles/Scrollbar.module.css';
  overflow-y: scroll;
  height: 500px;
  margin-left: 56px;
}
.center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.divider {
  padding-top: 50px;
}
.settingRow {
  display: flex;
  vertical-align: middle;
  align-items: center;
  justify-content: space-between;
}
.settingRow + .settingRow {
  margin-top: 8px;
}
.settingInputNumber {
  width: 72px;
}
.settingTitle {
  color: var(--bttv-rs-text-heading);
  font-size: 20px;
}
.settingTagInput {
  width: 300px;
}
.settingTagInput input {
  color: var(--bttv-rs-text-primary);
}
.panelWithOverflow {
  overflow: visible;
}
.codeBlock {
  background-color: var(--bttv-rs-sidenav-default-bg);
  padding: 2px 4px;
  border-radius: 4px;
  font-family: monospace;
  white-space: nowrap;
}
/*# sourceMappingURL=header.module.css.map */